import { Query } from "./query";

export class Aggregation {

    private name: string;
    private data: any = {};

    constructor(name: string) {
        this.name = name;
    }

    public static make(name: string): Aggregation {
        return new Aggregation(name);
    }

    public field(name: string, data: any = {}): this {
        this.data[name] = data;
        return this;
    }

    public addToQuery(query: Query): this {
        query.aggs(this);
        return this;
    }

    public toJSON() {
        let obj: any = {};
        obj[this.name] = this.data;

        return obj;
    }
}